/*
** website navbar links
*/
#navbar{
    background-color : $background-color;
    display : block;
    @extend %navbar_box_shadow;
    & > div{
        & > ul{
            margin : 0px;
            padding : 0px;
            & > li{
                display : inline-block;
                position: relative;
                transition : $transition;
                &:hover{
                    z-index: 11;
                }
                & > div {
                    transition : $transition;
                    
                    // the hover of drobdown
                    &.hasSub{
                        &::before{
                            content: '';
                            background : $background-hover;
                            position: absolute;
                            top: -20px;
                            left: -35px;
                            right: -35px;
                            display: block;
                            height: 20px;
                            opacity : 0;
                            transition : $transition;
                        }
                        &::after{
                            position: absolute;
                            right: -54px;
                            top: -20px;
                            width: 0;
                            content: '';
                            height: 0;
                            border-style: solid;
                            border-width: 20px 0 0 20px;
                            border-color: transparent transparent transparent $background-shadow ;
                            transition : $transition;
                            opacity: 0;
                        }
                        &:hover ul{
                            opacity : 1 !important;
                        }
                        &:hover .bottom{
                            opacity : 1 !important;
                        }

                    }

                    &:hover{
                        background : $background-hover;
                        &::after, &::before{
                            opacity : 1 !important;
                        }
                    }

                    &:hover a{
                        color : $color-active !important;
                    }
                    & > a{
                        padding : 40px 35px;
                        color : $color;
                        display : inline-block;
                        text-decoration : none;
                        font-size: 23px;
                        font-family: $font-family;
                        font-weight: normal;
                        line-height: normal;
                        transition : $transition;
                    }
                }

                ul{
                    position : absolute;
                    top : 80px;
                    left : 0px;
                    width : 100%;
                    opacity : 0;
                    transition : $transition;
                    padding-bottom: 20px;
                    background: $background-hover;
                    &::after{
                        z-index: -1;
                        background: $background-hover;
                        left: -35px;
                        right: -35px;
                        bottom: 0;
                        margin: auto;
                        content: '';
                        position: absolute;
                        top: -100px;
                    }
                    li {
                        display : block;
                        background-color : $background-hover;
                        position : relative;
                        &::before{
                            position: absolute;
                            left: 0px;
                            top: 0px;
                            bottom: 0;
                            margin: auto;
                            content: '';
                            width: 15px;
                            height: 13px;
                            background: url(/images/navbar/right-arrow.webp) no-repeat;
                        }
                        a{
                            padding : 10px;
                            margin : 0px;
                            color : $color-active;
                            font-size: 18px;
                            margin-left : 20px;
                            &:hover{
                                color : $color-hover !important;
                            }
                        }
                    }
                    img{
                        width : 100%;
                    }
                }

                .bottom{
                    transition: $transition;
                    left: -35px;
                    right: -35px;
                    top: 100%;
                    position: absolute;
                    margin: auto;
                }
            }
        }
    }
}