/**
** project banners
**/
// homepage 
#homepageBanner{
    height : 530px;
    .logo{
        float : left;
        img{
            width: 262px;
            border-width: 0px;
            border: 0px;
            max-width: 220px;
            height: auto;
            margin-top: 25px;
            margin-left: 15px;
        }
    }
    .logo_contact{
        text-align : center;
    }
    .contact_container{
        margin: 0;
        padding-top: 30px;
        left: 0;
        position: absolute;
        top: 0;
        width: 100%;
    }
    .contact{
        display: inline-block;
        // margin-left: 14%;
        font-size: 20px;
        text-shadow: 26px 0px 27px #2a313d;
        color: #333;
        font-family: Lato;
        font-weight: 400;
        span , a{
            color : #333;
            text-decoration : none;
        }
        .contact_us_link{
            color : $background-hover;
            transition : $transition;
            &:hover{
                color : $color-hover;
            }
        }
    }

    .demo{
        padding: 200px 0 150px;
        text-align : center;
        h2{
            width: 450px;
            display: block;
            max-width: 100%;
            margin: 0 auto;
            font-family : $font-family;
            font-size: 32px;
            line-height: 36px;
            padding : 0px;
        }
        a{
            display : inline-block;
            color: #1d4ca1;
            font-size: 23px;
            font-family: HelveticaNeue-Thin;
            line-height: 24px;
            padding: 20px 5px;
            min-width: 245px;
            border: 1px solid #949393;
            position: relative;
            text-decoration : none;
            margin-top : 25px;
            transition : $transition;
            &:hover{
                color: $background-hover;
                background: $color-hover;
            }
            &::before{
                content: '';
                margin-right: 12px;
                display: inline-block;
                position: relative;
                top: 2px;
                width: 0;
                height: 0;
                border-style: solid;
                border-width: 0 0 20px 20px;
                border-color: transparent transparent $background-hover transparent;
            }
        }
    }
}